<template>
<div v-if="user" class="sidebar" :data-background-color="backgroundColor" :data-active-color="activeColor">
    <div class="sidebar-wrapper" id="style-3">
        <div class="logo">
            <a href="#" class="simple-text">
            <div class="logo-img">
              
                <img v-if="user.profile_picture" :src="'data:image/png;base64,'+ user.profile_picture" alt="">
                <img v-else src="@/assets/img/user.png" alt="">
            </div>
             
          <span class="first-name-container"><drop-down  :type="'default'" tag="div" :title="displayName">
             <a href="/#/edit-profil" class="dropdown-item">Profile</a>
              <a href="/#/my-devices" class="dropdown-item">Devices</a>
               <a href="/#/send-feedback" class="dropdown-item">Feedback</a>
            <a @click="logout()" class="dropdown-item">Log out</a>
            </drop-down></span>
            <div class="username-container"><span> {{user.username}}</span></div>
            </a>
        </div>
        <slot>

        </slot>

        <ul class="nav">
            <moving-arrow :move-y="arrowMovePx">

            </moving-arrow>
            <slot name="links">

                <sidebar-link v-for="(link,index) in sidebarLinks" :key="index" :to="link.path" :name="link.name" :icon="link.icon">
                </sidebar-link>
            </slot>
        </ul>
        <div class="container-footer">
            <router-link to="new-contact"> <button class="newKey"><b>NEW CONTACT</b></button></router-link>
            <router-link to="share-key"> <button class="shareKey"><b>SHARE KEY</b></button></router-link>
        </div>
    </div>
</div>
</template>

<script>
import MovingArrow from "./MovingArrow.vue";
import SidebarLink from "./SidebarLink";
import {
    dataRepository
} from '@/repository/login'
export default {
    props: {
        backgroundColor: {
            type: String,
            default: "black",
            validator: value => {
                let acceptedValues = ["white", "black", "darkblue"];
                return acceptedValues.indexOf(value) !== -1;
            }
        },
        activeColor: {
            type: String,
            default: "success",
            validator: value => {
                let acceptedValues = [
                    "primary",
                    "info",
                    "success",
                    "warning",
                    "danger"
                ];
                return acceptedValues.indexOf(value) !== -1;
            }
        },
        sidebarLinks: {
            type: Array,
            default: () => []
        },
        autoClose: {
            type: Boolean,
            default: true
        }
    },
    provide() {
        return {
            autoClose: this.autoClose,
            addLink: this.addLink,
            removeLink: this.removeLink
        };
    },
    components: {
        MovingArrow,
        SidebarLink
    },
    computed: {
        /**
         * Styles to animate the arrow near the current active sidebar link
         * @returns {{transform: string}}
         */
        arrowMovePx() {
            return this.linkHeight * this.activeLinkIndex;
        }
    },
    data() {
        return {
            user: JSON.parse(sessionStorage.getItem('currentUser')),
            displayName: '',
            linkHeight: 65,
            activeLinkIndex: 0,
            windowWidth: 0,
            isWindows: false,
            hasAutoHeight: false,
            links: []
        };
    },
    methods: {
        findActiveLink() {
            this.links.forEach((link, index) => {
                if (link.isActive()) {
                    this.activeLinkIndex = index;
                }
            });
        },
        logout() {
            window.sessionStorage.removeItem('currentUser')
            window.sessionStorage.removeItem('token')
            return this.$router.push('login')
        },
        addLink(link) {
            const index = this.$slots.links.indexOf(link.$vnode);
            this.links.splice(index, 0, link);
        },
        removeLink(link) {
            const index = this.links.indexOf(link);
            if (index > -1) {
                this.links.splice(index, 1);
            }
        }
    },
    mounted() {
        if (this.user) {
            this.displayName = this.user.first_name + ' ' + this.user.last_name
        }

        this.$watch("$route", this.findActiveLink, {
            immediate: true
        });
    }
};
</script>

<style>
.container-footer {
    position: fixed;
    bottom: 0;
    width: 260px;
}

.shareKey {
    border-radius: 6px;
    background-color: #ff5c00;
    border: solid 1px #b3b3b3;
    color: white;
    border: none;
    width: 227px;
    height: 44px;
    cursor: pointer;
    margin-bottom: 16px;
    margin-left: 16px;
    margin-right: 16px;
}

.newKey {
    cursor: pointer;
    border: solid 1px #b3b3b3 !important;
    border-radius: 6px;
    background-color: transparent;
    color: #b3b3b3;

    border: none;
    width: 227px;
    height: 44px;
    margin-bottom: 8px;
    margin-left: 16px;
    margin-right: 16px;
}

.arrowLog {
    font-size: xx-small;
    color: #a6a6a6;
}

.first-name-container {
    position: absolute;
    top: 20px;

}

.left-margin {
    margin-left: 5px;
}

.username-container {
    position: absolute;
    left: 80px;
    top: 40px;
    font-size: smaller;
    color: #a6a6a6;
}
</style>
