            <div v-if="store.state.keychain.keylog.length > 0">
                <li v-for="i in orderLog.slice(0, defaultIndexNotification)" v-if="i.fullName !== undefined" class="item">
                    <p class="text-item"> <img v-if="i.picture !== undefined" class="detail-item notification-profile" :src="'data:image/png;base64,'+ i.picture">
