</template>

<script>
import {
    dataRepository
} from "@/repository/login";
import store from "@/flulux/store";
import moment from "moment";
import lodash from "lodash";
import repository from "@/repository/keychain";
import {
    keychainRepository
} from "@/repository/keychain";
export default {
    data() {
        return {
            store: store,
            keychainRepository: keychainRepository,
            defaultIndexNotification: 5
        };
    },
    computed: {
        orderLog() {
            return this.store.state.keychain.keylog.sort(
                (a, b) => new Date(b.created_at) - new Date(a.created_at)
            );
        }
    },
    methods: {
        /**
         * get time notification
         */
        getDiffTime(p) {
            const now = moment.utc(p).local();
            const end = moment.utc().local();
            return moment.duration(now.diff(end)).humanize();
        }
    }
};
</script>

