<template>
<div>
    <p class="sub-title-step">{{store.state.shareKey.lock_list_ids.length}} keys selected. You can also <span @click="setOrUnset(true)" class="sub-title-actions">select all</span> or <span @click="setOrUnset(false)" class="sub-title-actions">clear all</span></p>
<div class="users">
     <div v-if="group.doorOwned.length > 0" class="user-container" :class="{'open-container':group.showDetail }" :key="group.description" v-for="(group, i) in groups">
    <div @click="setActive(group, i)"><span class="name-container">  <img class="avatar-activity" v-if="group.lock_group_type !== 'personal'"  src="@/assets/img/group.png">
    <div v-else class="personal-container">
    <img class="avatar-activity"   src="@/assets/img/home.png">
    </div>
    <ul class="group-list"><li>
      
      
      <span>{{getGroupName(group.parent, group.description, group.parent_lock_group)}}</span>
     
      </li>
    <li class="sub-title-rules">{{group.doorOwned.length}} keys</li></ul>
        <img :disabled="group.doorOwned.length === 0"   :class="{'active-arrow': group.showDetail, 'inactive-arrow': !group.showDetail}"  class="selected-img" src="@/assets/havr-icon/right-arrow.svg">
    </span>
     </div>
      <div @click="chooseKey(lock, i, index)"  class="lock-container" v-if="group.showDetail" v-for="(lock, index) in group.doorOwned" :key="lock.id">
         <p  class="lock-label">{{lock.description}} <img v-if="lock.selected"  class="selected-img" src="@/assets/img/outline.png"></p>
          </div>
              <hr class="group-separator">
          </div>

 </div>
</div>

</template>
<script>
import store from "@/flulux/store";
import repository from "@/repository/keychain";
import lodash from "lodash";
import { createReadStream } from "fs";
export default {
  data() {
    return {
      store: store,
      groups: [],
      selectedKeys: [],
      selectedGroups: []
    };
  },
  async mounted() {
    if (this.store.state.shareKey.shallowCopy.length > 0) {
      return (this.groups = this.store.state.shareKey.shallowCopy);
    }
    await this.getDoorlocksWithGroups();
  },
  methods: {
    async getDoorlocksWithGroups() {
      const shared = lodash.filter(await repository.retrieveSharedKeys(), {
        admin: true
      });
      const res = await repository.getLockGroupsWithDoorlocks();
      const groups = res.lock_groups;
      const locks = res.door_locks;

      const idSharedKey = shared.map(i => i.door_lock.lock_group_id);

      const sharedGroups = await Promise.all(
        idSharedKey.map(async id => {
          const res = await repository.getLockGroupFromId(id);
          return res;
        })
      );

      const mapShared = lodash.uniqBy(sharedGroups, "id").map(i => ({
        ...i,
        doorOwned: lodash.uniqBy(
          lodash.filter(shared, {
            door_lock: { lock_group_id: i.id }
          }),
          "description"
        ),
        showDetail: false
      }));

      locks.map(i => {
        i.selected = false;
      });
      groups.map(i => {
        i.doorOwned = [];
        i.showDetail = false;
        i.parent = null;
        i.locks.map(lock => {
          lodash.uniqBy(
            i.doorOwned.push(lodash.filter(locks, { id: lock })[0]),
            "description"
          );
        });
        i.doorOwned.forEach(door => (door.door_lock_id = door.id));
      });

      await this.setData(groups, mapShared);
    },
    setActive(group, i) {
      group.showDetail = !group.showDetail;
      this.$set(this.groups, i, group);
    },
    getGroupName(parent, child, lockGroup) {
      if (lockGroup) return lockGroup.description;
      if (parent) return parent;
      else return child;
    },
    async setData(groups, mapShared) {
      groups.forEach(
        async i => (i.parent = await this.getLockGroupFromId(i.lock_group_id))
      );
      mapShared.forEach(
        async i => (i.parent = await this.getLockGroupFromId(i.lock_group_id))
      );

      this.groups = [...groups, ...mapShared];
    },
    chooseKey(lock, i, index) {
      if (lock.selected) lock.selected = false;
      else lock.selected = true;
      this.$set(this.groups[i].doorOwned[index], i, lock);
      this.createObjectForStore();
    },
    createObjectForStore() {
      this.selectedKeys = [];
      this.selectedGroups = [];
      const keysToShare = lodash.filter(this.groups, {
        doorOwned: [{ selected: true }]
      });
      keysToShare.forEach(i => {
        i.doorOwned.forEach(lock => {
          if (lock.selected) {
            this.selectedKeys.push(lock.door_lock_id);
            this.selectedGroups.push(i.lock_group_id);
          }
        });
      });
      this.store.actions.SET_SHARED_KEYS_TO_SHARE(
        lodash.uniq(this.selectedKeys)
      );
      this.store.state.shareKey.shallowCopy = this.groups;
    },
    async getLockGroupFromId(p) {
      const res = await repository.getLockGroupFromId(p);
      if (res !== undefined && !res.error) {
        return res.description;
      }
    },
    setOrUnset(p) {
      if (p) this.notifySelection("All key selected");
      if (!p) this.notifySelection("All key unselected");
      this.groups.forEach((i, index) => {
        i.doorOwned.forEach(lock => {
          if (lock) {
            lock.selected = p;
          }
        });
      });
      this.$set(this.groups);
      this.createObjectForStore();
    },
    notifySelection(p) {
      this.$notify({
        message: p,
        type: "info",
        horizontalAlign: "right",
        timeout: 2000,
        verticalAlign: "top"
      });
    }
  }
};
</script>

