<template>
<div>

    <span class="sub-title-step">{{selectedUser}} users selected. You can also <span @click="setOrUnset(true)" class="sub-title-actions">select all</span> or <span @click="setOrUnset(false)" class="sub-title-actions">clear all</span></span>
<div class="users">
 <div @click="selectUser(user, i)" :key="user.id"  class="user-container" v-for="(user, i) in users">
    <img v-if="user.picture" class="avatar-activity" :src="'data:image/png;base64,'+ user.picture"> 
    <img v-else class="avatar-activity" src="@/assets/img/user.png"><span class="name-container">{{user.user.first_name}}  {{user.user.last_name}}
        <img v-if="user.selected"  class="selected-img" src="@/assets/img/outline.png">
    </span>
    
 </div>
</div>
</div>
</template>
<script>
import store from "@/flulux/store";
import userRepository from "@/repository/users";
import lodash from "lodash";
export default {
  data() {
    return {
      users: [],
      store: store
    };
  },
  async mounted() {
    const res = await this.getUsers();
    if (this.store.state.shareKey.user_list_ids.length > 0) {
      this.mapSelectedFromStore();
    }
  },
  computed: {
    selectedUser() {
      const val = lodash.filter(this.users, { selected: true }).length;
      return val;
    }
  },
  methods: {
    async getUsers() {
      const users = await userRepository.retrieveContacts();
      const picture_profile = await userRepository.retrieveContactPictures(
        this.mapUsersId(users)
      );
      this.mapPictureProfile(picture_profile, users);
    },
    mapUsersId(p) {
      return p.map(i => i.user.id);
    },
    mapPictureProfile(picture_profile, users) {
      users.map(user => {
        user.selected = false;
        picture_profile.map(picture => {
          if (user.user.id === picture.user_id) {
            user.picture = picture.base64;
          }
        });
        this.users.push(user);
      });
    },
    mapSelectedFromStore() {
      this.store.state.shareKey.user_list_ids.forEach(id => {
        this.users.forEach((user, i) => {
          if (user.id === id) {
            user.selected = true;
            this.$set(this.users, i, user);
          }
        });
      });
    },
    setOrUnset(p) {
      if(p) this.notifySelection('All contacts selected')
      if(!p) this.notifySelection('All contacts unselected')
      this.users.forEach((i, index) => {
        i.selected = p;
        this.$set(this.users, index, i);
      });
      this.setUsersId();
    },

     notifySelection(p) {
      this.$notify({
        message: p,
        type: "info",
        horizontalAlign: "right",
        timeout: 2000,
        verticalAlign: "top"
      });
    },
    selectUser(p, i) {
      p.selected = !p.selected;
      this.$set(this.users, i, p);
      this.setUsersId();
    },
    setUsersId() {
      const filter = lodash.filter(this.users, { selected: true });
      const userIds = filter.map(user => user.user.id);
      this.store.actions.SET_SHARED_KEY_USERS(userIds);
    }
  }
};
</script>

<style scoped>
.users {
  margin-left: 6em;
}
.avatar-activity {
  width: 30px;
  height: 30px;
  border-radius: 30px;
  margin-right: -10px;
  border: 2px solid white;
}

.user-container {
  height: 60px;
  cursor: pointer;
}

.name-container {
  margin-left: 20px;
}

.sub-title-actions {
  color: #ff5c00;
  font-weight: 600;
  cursor: pointer;
}

.selected-img {
  height: 24px;
  width: 24px;
  position: absolute;
  right: 6em;
  z-index: 2;
}

.sub-title-step {
  position: relative;
  left: 6em;
  color: #a6a6a6;
  bottom: 2em;
}
</style>


