<template>
<div>
    <router-link to="/keychain"><img class="close-account-page" src='@/assets/havr-icon/close-drawer-icon.svg'></router-link>
        <div class="layout-body">
            <div class="jumbo">
                <p class="container-title"><b>Send us a message</b></p>
            </div>

            <div class="section-form">
                <div>
                    <div class="form-group">
                        <div class="form-group ">
                            <label>Category</label>
                            <select v-model="category" class="form-control havr-black-input">
      <option>Feedback</option>
      <option>Bug report</option>
      <option>Customer service</option>
    </select>
                        </div>

                        <div class="form-group">
                            <label>Message</label>
                            <textarea v-model="message" class="form-control havr-black-input havr-black-textarea" rows="3"></textarea>
                        </div>
                    </div>
                    <button :disabled="!message" @click="sendMessage" class="btn btn-primary havr-black-color" >Send</button>
                </div>

            </div>
        </div>

</div>
</template>

<script>
import notifiy from "vue-notification";
import repository from "@/repository/misc";

export default {
  data() {
    return {
      message: null,
      category: "Feedback"
    };
  },
  methods: {
      /**
       * Select route to call via category type
       */
    sendMessage() {
      const message = { message: this.message };
      if (this.category === "Feedback") return this.sendFeedBack(message);
      if (this.category === "Bug report") return this.sendBugReport(message);
      if (this.category === "Customer service")
        return this.sendCustomerService(message);
    },
    /**
     * Call /feedback api route
     */
    async sendFeedBack(message) {
      const res = await repository.sendFeedBack(message);
      this.notifyUser(this.isErr(res));
    },
    /**
     * Call bugreport api route 
     */
    async sendBugReport(message) {
      const res = await repository.sendBugReport(message);
      this.notifyUser(this.isErr(res));
    },
    /**
     * Call customerservice api route
     */
    async sendCustomerService(message) {
      const res = await repository.sendCustomerService(message);
      this.notifyUser(this.isErr(res));
    },
    /**
     * Generic error builder from api response
     */
    isErr(res) {
      if (!res) return { message: "Internal error occured", type: "danger" };
      return { message: "Message send with succes", type: "success" };
    },
    /**
     * Notification toaster plugin
     */
    notifyUser(p) {
       this.$notify({
        message: p.message,
        type: p.type,
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });
      this.message = null
    }
  }
};
</script>

<style lang="scss">
@import 'sendFeedback.css'
</style>
