import { ENDPOINTS, API_BASE_URL } from '../constant.js'

export default {
    /**
     * call /api/feedback
     * @param {message : message} p 
     */
    async sendFeedBack(p) {
        const resp = await fetch(API_BASE_URL + ENDPOINTS.feedback, {
            method: 'post',
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + sessionStorage.getItem('token') },
            body: JSON.stringify({ feedback: p })
        })
        if (resp.status === 204) {
            return true
        }
        return false
    },
    /**
   * call /api/report_bug
   * @param {message : message} p 
   */
    async sendBugReport(p) {
        const resp = await fetch(API_BASE_URL + ENDPOINTS.report_bug, {
            method: 'post',
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + sessionStorage.getItem('token') },
            body: JSON.stringify({ bug_report: p })
        })
        if (resp.status === 204) {
            return true
        }
        return false
    },
    /**
   * call /api/custom_service
   * @param {message : message} p 
   */
    async sendCustomerService(p) {
        const resp = await fetch(API_BASE_URL + ENDPOINTS.custom_service, {
            method: 'post',
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + sessionStorage.getItem('token') },
            body: JSON.stringify({ custom_service: p })
        })
        if (resp.status === 204) {
            return true
        }
        return false
    },
    /**
     *  call /api/devices
     */
    async getDevices() {
        const resp = await fetch(API_BASE_URL + ENDPOINTS.devices, {
            method: "GET",
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + sessionStorage.getItem('token') }
        })
        const res = await resp.json()
        if (res) return res
        return false
    },
    /**
     * Call /api/devices/:id
     * @param {id: Number} id 
     */
    async deleteDevice(id, password) {
        const resp = await fetch(API_BASE_URL + ENDPOINTS.devices + '/' + id, {
            method: 'DELETE',
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + sessionStorage.getItem('token') },
            body: JSON.stringify({user: password})
        })

        if (resp.status === 200) {
            return true
        }
        return false
    }
}